.sidebar {
    background-color: #f4f4f4;
    padding: 15px;
    border-right: 1px solid #ddd;
  }
  
  .sidebar-header h3 {
    margin: 0;
    padding-bottom: 10px;
    border-bottom: 1px solid #ddd;
  }
  
  .sidebar-content ul {
    list-style: none;
    padding: 0;
    margin-top: 10px;
  }
  
  .sidebar-content ul li a {
    text-decoration: none;
    color: #333;
    display: block;
    padding: 5px 0;
  }
  